<template>
  <div id="app">
    <Header></Header>
    <div
      style="width: 95vw; height: 80vh;"
      v-if="!loadData"
      class="d-flex justify-content-center align-items-center"
    >
      <b-spinner style="width: 3rem; height: 3rem;" label="Large Spinner"></b-spinner>
    </div>
    <main v-else>
      <Filters
        @changeKateg="changeCateg"
        @changePrice="changePrice"
        @changeFav="changeFav"
        :products="products"
      ></Filters>

      <section class="options-block">
        <b-container class="container options-block__wrap">
          <div class="options-block__header pb-1">Отсортировать по:</div>
          <div class="options-block__options-wrap">
            <div class="filter mb-3 mb-md-0">
              <div class="filter__controls">
                <b-form-select class="filter__select mr-3" v-model="typeSort">
                  <option value="rating">По популярности (рейтинг продавца)</option>
                  <option value="price">По цене</option>
                </b-form-select>
                <b-form-select v-model="directionSort" class="filter__select">
                  <option value="down">По убывания</option>
                  <option value="up">По возрастанию</option>
                </b-form-select>
              </div>
            </div>
          </div>
        </b-container>
      </section>

      <section class="pt-4 pb-4">
        <b-container class="container">
          <h1>Товаров: {{productFiltered.length}}</h1>
        </b-container>
      </section>

      <section class="products">
        <div class="container products__wrapper">
          <Product
            v-for="product in productFiltered"
            :key="product.id"
            :product="product"
            :salers="findSalers(product.relationships.seller)"
          ></Product>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import Product from "./components/Product";
import Header from "./components/Header";
import Filters from "./components/Filters";

export default {
  components: {
    Product,
    Header,
    Filters
  },
  data() {
    return {
      products: [],
      productsOrig: [],
      sellers: [],
      categ: "",
      loadData: false,
      beginPrice: 0,
      endPrice: 0,
      showFav: false,
      typeSort: "price",
      directionSort: "down"
    };
  },
  mounted() {
    fetch("https://avito.dump.academy/products")
      .then(data => data.json())
      .then(({ data }) => {
        this.products = data;
        this.productsOrig = data;
      })
      .then(() => {
        fetch("https://avito.dump.academy/sellers")
          .then(data => data.json())
          .then(({ data }) => {
            this.sellers = data;
            this.loadData = true;
          });
      });
  },
  methods: {
    changePrice({ begprice, endPrice }) {
      this.beginPrice = begprice;
      this.endPrice = endPrice;
    },
    changeCateg(categName) {
      this.categ = categName;
    },
    findSalers(id) {
      return this.sellers.find(el => {
        return el.id == id;
      });
    },
    changeFav(value) {
      if (value) {
        const massIndexFav = JSON.parse(localStorage.fav);
        const massFav = [];
        massIndexFav.forEach(indexFav => {
          const favItem = this.productsOrig.find(el => el.id == indexFav);
          massFav.push(favItem);
        });
        this.products = massFav;
      } else {
        this.products = this.productsOrig;
      }
    }
  },
  computed: {
    productFiltered: {
      get: function() {
        return (
          this.products
            // фильтр по категории
            .filter(el => {
              return !this.categ || el.category == this.categ;
            })
            // фильтр по ценам
            .filter(product => {
              if (!product.price) return true;
              return (
                Number(product.price) >= this.beginPrice &&
                Number(product.price) <= this.endPrice
              );
            })
            //сотрировка
            .sort((lastOne, nextOne) => {
              //по рейтингу
              if (this.typeSort == "rating") {
                const ratePrev = this.findSalers(lastOne.relationships.seller)
                  .rating;
                const reteNext = this.findSalers(nextOne.relationships.seller)
                  .rating;
                if (this.directionSort == "up") {
                  return ratePrev > reteNext ? 1 : -1;
                } else {
                  return ratePrev < reteNext ? 1 : -1;
                }
                //по цене
              } else if (this.typeSort == "price") {
                const pricePrev = lastOne.price;
                const priceNext = nextOne.price;
                if (this.directionSort == "up") {
                  return pricePrev > priceNext ? 1 : -1;
                } else {
                  return pricePrev < priceNext ? 1 : -1;
                }
              }
            })
        );
      }
    },
    set: function(newValue) {
      this.products = newValue;
    }
  }
};
</script>


